<template>
  <v-app>
    <v-main>
      <v-container class="text-center">
        <h1>Custom QR Code Scanner</h1>

        <!-- Nút để bắt đầu quét -->
        <v-btn color="primary" large @click="startScanning">
          Start Scanning
        </v-btn>

        <!-- Quét mã QR -->
        <qrcode-stream
          v-if="showScanner"
          @decode="onDecode"
          @init="onInit"
          :paused="paused"
          class="mt-4"
          :facingMode="'environment'"
        ></qrcode-stream>

        <!-- Hiển thị kết quả quét -->
        <v-alert v-if="decodedText" type="success" class="mt-4">
          Decoded: {{ decodedText }}
        </v-alert>

        <!-- Hiển thị lỗi -->
        <v-alert v-if="errorMessage" type="error" class="mt-4">
          {{ errorMessage }}
        </v-alert>

        <!-- Nút Restart Scanner -->
        <v-btn class="mt-4" color="primary" @click="stopScanner">
          Stop Scanning
        </v-btn>
        <input
          type="file"
          accept="image/*"
          @change="onFileChange"
          class="mt-4"
        />
        
        <!-- Hiển thị ảnh đã chọn -->
        <div v-if="selectedImage" class="mt-4">
          <img :src="selectedImage" alt="QR Code Image" class="preview-image"/>
        </div>
        <h1>Camera Video Recorder</h1>
        <v-row justify="center">
      <v-col cols="12" md="8">
        <v-card>
          <v-card-title>Quay Video</v-card-title>
          <v-card-text>
            <!-- Hiển thị video từ camera -->
            <video ref="video" autoplay playsinline style="width: 100%; height: auto;" />

            <!-- Hiển thị nút điều khiển -->
            <v-btn
              color="primary"
              class="mt-4"
              @click="startRecording"
              :disabled="isRecording"
            >
              Bắt đầu Quay
            </v-btn>
            <v-btn
              color="error"
              class="mt-4"
              @click="stopRecording"
              :disabled="!isRecording"
            >
              Dừng Quay
            </v-btn>
          </v-card-text>
          <v-card-actions>
            <v-btn color="success" @click="downloadVideo" v-if="videoBlob">
              Tải Video Xuống
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { QrcodeStream } from "vue3-qrcode-reader";

export default {
  components: { QrcodeStream },
  data() {
    return {
      decodedText: "",
      errorMessage: "",
      showScanner: false,
      paused: false,
    };
  },
  methods: {
    // Bắt đầu quét mã QR
    startScanning() {
      this.showScanner = true;
      this.errorMessage = "";
      this.decodedText = "";
    },
    // Khi quét thành công
    onDecode(decodedString) {
      this.decodedText = decodedString;
      this.errorMessage = "";
      this.paused = true; // Tạm dừng quét sau khi quét thành công
    },
    // Khi khởi tạo camera
    onInit(promise) {
      promise.catch(() => {
        this.errorMessage =
          "Unable to access camera. Please allow camera permissions or check your browser settings.";
      });
    },
    // Dừng quét mã QR
    stopScanner() {
      this.paused = false;
      this.decodedText = "";
      this.errorMessage = "";
    },
    async startRecording() {
      try {
        // Truy cập camera và hiển thị trong video element
        this.stream = await navigator.mediaDevices.getUserMedia({ video: true });
        this.$refs.videoElement.srcObject = this.stream;

        // Khởi tạo MediaRecorder để quay video
        this.mediaRecorder = new MediaRecorder(this.stream);
        this.mediaRecorder.ondataavailable = this.onDataAvailable;
        this.mediaRecorder.onstop = this.onStopRecording;

        // Bắt đầu quay video
        this.mediaRecorder.start();
        this.isRecording = true;
      } catch (error) {
        console.error("Error accessing camera: ", error);
      }
    },

    onDataAvailable(event) {
      // Lưu lại các đoạn video đã quay
      this.recordedChunks.push(event.data);
    },

    onStopRecording() {
      // Khi quay xong, tạo videoBlob và tên file
      this.videoBlob = new Blob(this.recordedChunks, { type: "video/webm" });
      this.videoSrc = URL.createObjectURL(this.videoBlob);
      this.videoName = `video_${Date.now()}.webm`; // Tên video theo thời gian
    },

    stopRecording() {
      // Dừng quay video
      if (this.mediaRecorder && this.isRecording) {
        this.mediaRecorder.stop();
        this.isRecording = false;
      }
    },

    saveVideo() {
      // Lưu video vào máy người dùng
      if (this.videoBlob) {
        // saveAs(this.videoBlob, this.videoName);
      }
    },
  },
};
</script>

